.form {
  width: 1280px;
  height: 680px;
  padding: 36px 80px 67px 80px;
}

.form__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.form__title {
  margin: 0;
  margin-bottom: 24px;
  font-size: 36px;
  line-height: 44px;
  font-family: inherit;
  text-align: center;
  color: $basic-white;
  text-shadow: 0 1px 1px $basic-black;
}

.form__items {
  display: flex;
}

.form__label {
  position: relative;
  z-index: 1;
  display: block;
  width: 320px;
  height: 509px;
  margin-right: auto;
}

.form__label:last-child {
  margin-right: 0;
}

.form__buy {
  margin: 0;
  margin-top: 14px;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  font-family: $basic-font-additional;
  text-align: center;
  color: $basic-white;
}

.form__link {
  color: $basic-curious-blue;
  text-decoration: none;
  border-bottom: 1px dashed $basic-curious-blue;
  cursor: pointer;
}

.form__buy--default {
  display: block;
}

.form__buy--checked {
  display: none;
}

.form__buy--disabled {
  display: none;
}

.form__label--active .form__link {
  color: $basic-picton-blue;
  border-bottom: 1px dashed $basic-picton-blue;
}

.form__label--active .card__color {
  fill: $basic-picton-blue;
}

.form__label--active .card__weight {
  background: $basic-picton-blue;
}

.form__checkbox:checked ~ .card .card__background--second .card__color {
  fill: $basic-razzmatazz;
}

.form__checkbox:checked ~ .card .card__weight {
  background: $basic-razzmatazz;
}

.form__checkbox:checked ~ .card .card__promo--active {
  color: $basic-razzmatazz;
}

.form__label--active .form__checkbox:checked ~ .card .card__background--second .card__color {
  fill: $basic-cerise-red;
}

.form__label--active .form__checkbox:checked ~ .card .card__weight {
  background: $basic-cerise-red;
}

.form__label--active .form__checkbox:checked ~ .card .card__promo {
  color: $basic-cerise-red;
}

.form__checkbox:checked ~ .form__buy.form__buy--default {
  display: none;
}

.form__checkbox:checked ~ .form__buy.form__buy--checked {
  display: block;
}

.form__checkbox:disabled ~ .form__buy {
  color: $basic-laser-lemon;
}

.form__checkbox:disabled ~ .form__buy.form__buy--default {
  display: none;
}

.form__checkbox:disabled ~ .form__buy.form__buy--disabled {
  display: block;
}

.form__checkbox:disabled ~ .card .card__image {
  opacity: 0.5;
}

.form__checkbox:disabled ~ .card .card__promo {
  color: $basic-nobel;
  opacity: 0.5;
}

.form__checkbox:disabled ~ .card .card__title {
  color: $basic-nobel;
  opacity: 0.5;
}

.form__checkbox:disabled ~ .card .card__item {
  color: $basic-nobel;
  opacity: 0.5;
}

.form__checkbox:disabled ~ .card .card__weight {
  background: $basic-nobel;
}

.form__checkbox:disabled ~ .card .card__color {
  fill: $basic-nobel;
}

@media (max-width: $width-desktop - 1) {
  .form {
    width: 768px;
    height: auto;
    padding-right: 40px;
    padding-left: 40px;
  }

  .form__items {
    flex-wrap: wrap;
    justify-content: center;
  }

  .form__label {
    margin-right: 0;
    margin-bottom: 40px;
  }

  .form__label:first-child {
    margin-right: 40px;
  }

  .form__label:last-child {
    margin-bottom: 0;
  }

  .form__label:nth-child(2n) {
    margin-right: 0;
  }

  .form__label:nth-child(3n) {
    margin-right: 40px;
  }
}

@media (max-width: $width-tablet - 1) {
  .wrapper {
    height: auto;
  }

  .wrapper::before {
    height: auto;
  }

  .form {
    width: 360px;
    height: auto;
    padding-right: 20px;
    padding-left: 20px;
  }

  .form__items {
    flex-wrap: wrap;
    justify-content: center;
  }

  .form__label {
    margin-right: 0;
    margin-bottom: 40px;
  }

  .form__label:first-child {
    margin-right: 0;
  }

  .form__label:last-child {
    margin-bottom: 0;
  }

  .form__label:nth-child(3n) {
    margin-right: 0;
  }
}
