@font-face {
  font-weight: 100;
  font-family: "Exo-2";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Exo-2-Thin.woff2") format("woff2"), url("../fonts/Exo-2-Thin.woff") format("woff"), url("../fonts/Exo-2-Thin.ttf") format("truetype"), url("../fonts/Exo-2-Thin.otf") format("opentype"); }

@font-face {
  font-weight: 400;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TrebuchetMS.woff2") format("woff2"), url("../fonts/TrebuchetMS.woff") format("woff"), url("../fonts/TrebuchetMS.ttf") format("truetype"), url("../fonts/TrebuchetMS.otf") format("opentype"); }

@font-face {
  font-weight: 700;
  font-family: "Trebuchet MS";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TrebuchetMS-Bold.woff2") format("woff2"), url("../fonts/TrebuchetMS-Bold.woff") format("woff"), url("../fonts/TrebuchetMS-Bold.ttf") format("truetype"), url("../fonts/TrebuchetMS-Bold.otf") format("opentype"); }

:root {
  --basic-font-default: "Exo-2", "Arial", sans-serif;
  --basic-font-additional: "Trebuchet MS", "Arial", sans-serif;
  --basic-black: #000000;
  --basic-white: #ffffff;
  --basic-dove-gray: #666666;
  --basic-nobel: #b3b3b3;
  --basic-laser-lemon: #ffff66;
  --basic-curious-blue: #1698d9;
  --basic-picton-blue: #2ea8e6;
  --basic-razzmatazz: #d91667;
  --basic-cerise-red: #e52e7a; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  height: 100%; }

body {
  display: flex;
  height: 100%;
  font-weight: 400;
  font-family: "Exo-2", "Arial", sans-serif;
  scroll-behavior: smooth; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px; }

.container--flex {
  display: flex; }

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  min-height: 100%;
  background: url("../img/background@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

@media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
  .wrapper {
    background-image: url("../img/background@2x.jpg"); } }

@media (max-width: 1279px) {
  body {
    height: auto; } }

.form {
  width: 1280px;
  height: 680px;
  padding: 36px 80px 67px 80px; }

.form__fieldset {
  margin: 0;
  padding: 0;
  border: none; }

.form__title {
  margin: 0;
  margin-bottom: 24px;
  font-size: 36px;
  line-height: 44px;
  font-family: inherit;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 1px #000000; }

.form__items {
  display: flex; }

.form__label {
  position: relative;
  z-index: 1;
  display: block;
  width: 320px;
  height: 509px;
  margin-right: auto; }

.form__label:last-child {
  margin-right: 0; }

.form__buy {
  margin: 0;
  margin-top: 14px;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  text-align: center;
  color: #ffffff; }

.form__link {
  color: #1698d9;
  text-decoration: none;
  border-bottom: 1px dashed #1698d9;
  cursor: pointer; }

.form__buy--default {
  display: block; }

.form__buy--checked {
  display: none; }

.form__buy--disabled {
  display: none; }

.form__label--active .form__link {
  color: #2ea8e6;
  border-bottom: 1px dashed #2ea8e6; }

.form__label--active .card__color {
  fill: #2ea8e6; }

.form__label--active .card__weight {
  background: #2ea8e6; }

.form__checkbox:checked ~ .card .card__background--second .card__color {
  fill: #d91667; }

.form__checkbox:checked ~ .card .card__weight {
  background: #d91667; }

.form__checkbox:checked ~ .card .card__promo--active {
  color: #d91667; }

.form__label--active .form__checkbox:checked ~ .card .card__background--second .card__color {
  fill: #e52e7a; }

.form__label--active .form__checkbox:checked ~ .card .card__weight {
  background: #e52e7a; }

.form__label--active .form__checkbox:checked ~ .card .card__promo {
  color: #e52e7a; }

.form__checkbox:checked ~ .form__buy.form__buy--default {
  display: none; }

.form__checkbox:checked ~ .form__buy.form__buy--checked {
  display: block; }

.form__checkbox:disabled ~ .form__buy {
  color: #ffff66; }

.form__checkbox:disabled ~ .form__buy.form__buy--default {
  display: none; }

.form__checkbox:disabled ~ .form__buy.form__buy--disabled {
  display: block; }

.form__checkbox:disabled ~ .card .card__image {
  opacity: 0.5; }

.form__checkbox:disabled ~ .card .card__promo {
  color: #b3b3b3;
  opacity: 0.5; }

.form__checkbox:disabled ~ .card .card__title {
  color: #b3b3b3;
  opacity: 0.5; }

.form__checkbox:disabled ~ .card .card__item {
  color: #b3b3b3;
  opacity: 0.5; }

.form__checkbox:disabled ~ .card .card__weight {
  background: #b3b3b3; }

.form__checkbox:disabled ~ .card .card__color {
  fill: #b3b3b3; }

@media (max-width: 1279px) {
  .form {
    width: 768px;
    height: auto;
    padding-right: 40px;
    padding-left: 40px; }
  .form__items {
    flex-wrap: wrap;
    justify-content: center; }
  .form__label {
    margin-right: 0;
    margin-bottom: 40px; }
  .form__label:first-child {
    margin-right: 40px; }
  .form__label:last-child {
    margin-bottom: 0; }
  .form__label:nth-child(2n) {
    margin-right: 0; }
  .form__label:nth-child(3n) {
    margin-right: 40px; } }

@media (max-width: 767px) {
  .wrapper {
    height: auto; }
  .wrapper::before {
    height: auto; }
  .form {
    width: 360px;
    height: auto;
    padding-right: 20px;
    padding-left: 20px; }
  .form__items {
    flex-wrap: wrap;
    justify-content: center; }
  .form__label {
    margin-right: 0;
    margin-bottom: 40px; }
  .form__label:first-child {
    margin-right: 0; }
  .form__label:last-child {
    margin-bottom: 0; }
  .form__label:nth-child(3n) {
    margin-right: 0; } }

.card {
  position: relative;
  width: 320px;
  height: 480px;
  padding: 21px 47px;
  border-radius: 12px;
  cursor: pointer; }

.card__background {
  position: absolute;
  top: 0;
  left: 0; }

.card__background--first {
  z-index: -1; }

.card__background--second {
  z-index: 0; }

.card__image {
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 12px;
  pointer-events: none; }

.card__promo {
  margin: 0;
  margin-bottom: 5px;
  padding: 0 3px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  color: #666666; }

.card__title {
  margin: 0;
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  color: #000000; }

.card__title--additional {
  display: block;
  padding: 0 3px;
  font-size: 24px;
  line-height: 28px; }

.card__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 0 3px; }

.card__item {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  color: #666666; }

.card__item b {
  font-weight: 700; }

.card__weight {
  position: absolute;
  right: 16px;
  bottom: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  padding-top: 5px;
  background: #1698d9;
  border-radius: 50%; }

.card__weight span {
  font-weight: 400;
  font-size: 42px;
  line-height: 22px;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  text-align: center;
  color: #ffffff; }

.card__weight sub {
  display: block;
  margin-top: 3px;
  font-size: 21px;
  line-height: 22px; }
